import CouponItme from '../../components/CouponItme/CouponItme.vue';
import {
	mapActions,
	mapGetters
} from "vuex";
export default {
	components: {
		CouponItme
	},
	data() {
		return {
			height: 64, //header高度
			top: 0, //标题图标距离顶部距离
			scrollH: 0, //滚动总高度
			opcity: 0,
			goodsId: 1,
			iconOpcity: 0.5,
			bannerIndex: 0,
			cardShow: false,
			stockNum: 0,
			topMenu: [{
				icon: "message",
				text: "消息",
				size: 26,
				badge: 3
			}, {
				icon: "home",
				text: "首页",
				size: 23,
				badge: 0
			}, {
				icon: "people",
				text: "我的",
				size: 26,
				badge: 0
			}, {
				icon: "cart",
				text: "购物车",
				size: 23,
				badge: 2
			}, {
				icon: "kefu",
				text: "客服小蜜",
				size: 26,
				badge: 0
			}, {
				icon: "feedback",
				text: "我要反馈",
				size: 23,
				badge: 0
			}, {
				icon: "share",
				text: "分享",
				size: 26,
				badge: 0
			}],
			menuShow: false,
			value: 1,
			collected: false,
			couponShow: false,
			GoodInfo: {},
			GoodsCoupon: [],
			isflash: 0,
			goodsNumber: 0
		}
	},
	onLoad: async function(options) {
		let obj = {};
		obj = wx.getMenuButtonBoundingClientRect();
		setTimeout(() => {
			uni.getSystemInfo({
				success: (res) => {
					this.width = obj.left || res.windowWidth;
					this.height = obj.top ? (obj.top + obj.height + 8) : (res.statusBarHeight + 44);
					this.top = obj.top ? (obj.top + (obj.height - 32) / 2) : (res.statusBarHeight + 6);
					this.scrollH = res.windowWidth
				}
			})
		}, 50)
		this.goodsId = options.goodsId;
		// this.isflash = options.isflash;
		//判断是否是秒杀商品
		this.$api.isFlashGoods({
			goodsId: this.goodsId
		}, res => {
			if (res.code) {
				this.isflash = res.data;
			}
			this.getGoodsInfoById()
			this.getGoodsCoupon()
			this.stockNum = this.getNameGoodListNum(this.goodsId).count
			this.goodsNumber = this.getNameGoodListNum(this.goodsId).goodsNumber
		})
	},
	computed: {
		...mapGetters({
			getGoodsList: "getGoodsList",
			getNameGoodListNum: 'getNameGoodListNum',
			getUserInfos: 'getUserInfos',
			getisLogin: 'getisLogin'
		})
	},
	methods: {
		bannerChange: function(e) {
			this.bannerIndex = e.detail.current
		},
		previewImage: function(e) {
			let index = e.currentTarget.dataset.index;
			uni.previewImage({
				current: this.GoodInfo.goodImgList[index],
				urls: this.GoodInfo.goodImgList
			})
		},
		showCard: function() {
			if (!this.goodsNumber) return;
			this.cardShow = true;
		},
		back: function() {
			uni.navigateBack()
		},
		hideCard: function() {
			this.cardShow = false;
		},
		openMenu: function() {
			this.menuShow = true
		},
		showCoupon: function() {
			this.couponShow = true;
		},
		closeCoupon: function() {
			this.couponShow = false;
		},
		closeMenu: function() {
			this.menuShow = false
		},
		change: function(e) {
			this.value = e.value
		},
		collecting: function() {
			this.collected = !this.collected
		},
		common: function() {
			this.tui.toast("功能开发中~")
		},
		submit() {
			// if (!this.getisLogin) {
			// 	this.$common.jumpToLogin();
			// 	return false;
			// }
			uni.switchTab({
				url: '../cart/cart'
			})
		},
		goodsNumberItemClick(goodsNumber) {
			this.goodsNumber = goodsNumber
			let {
				goodsId,
				goodsName
			} = this.GoodInfo;
			this.addCardAction({
				goodsId,
				goodsName,
				goodsNumber: this.goodsNumber,
				count: this.stockNum,
				isflash: this.isflash,
				selected: true
			})
		},
		bindChange: function(e) {
			this.stockNum = e.value;
			let {
				goodsId,
				goodsName
			} = this.GoodInfo;
			this.addCardAction({
				goodsId,
				goodsName,
				goodsNumber: this.goodsNumber,
				count: this.stockNum,
				isflash: this.isflash,
				selected: true
			})
		},
		submitOrder() {
			if (!this.getisLogin) {
				this.$common.jumpToLogin();
				return false;
			}
			if (!this.stockNum) {
				this.stockNum = 1
			}
			let {
				goodsId,
				goodsName
			} = this.GoodInfo;
			let goodsItemInfo = {
				goodsId,
				goodsName,
				isflash: this.isflash,
				count: this.stockNum,
				selected: true
			}
			// this.emptyAllCardAction()

			this.$api.getGoodsInfoById({
				goodsId: goodsId,
				isflash: this.isflash,
			}, (res) => {
				if (res.code) {
					goodsItemInfo.selected = true;
					goodsItemInfo.goodsNumber = this.goodsNumber
					goodsItemInfo.goodsInfo = res.data
					
					// this.addCardAction(goodsItemInfo)
					uni.setStorage({
						key: 'buylist',
						data: [goodsItemInfo],
						success: () => {
							uni.navigateTo({
								url: '../confirmation/confirmation?isOne=1'
							})
						}
					})
				}
			})
		},
		addCart() {
			if (this.stockNum) {
				this.$common.errorToShow("购物车已存在该商品");
				return;
			}
			let {
				goodsId,
				goodsName
			} = this.GoodInfo;
			this.addCardAction({
				goodsId,
				goodsName,
				goodsNumber: this.goodsNumber,
				isflash: this.isflash,
				selected: true
			})
			this.stockNum = 1;
		},
		getGoodsInfoById() {
			this.$api.getGoodsById({
				goodsId: this.goodsId,
				isflash: this.isflash
			}, (res) => {
				if (res.code) {
					this.GoodInfo = res.data
					if (this.GoodInfo.goodsNumber.length) {
						this.goodsNumber = this.goodsNumber || this.GoodInfo.goodsNumber[0]
					} else {
						this.$common.errorToShow('商品暂时缺货')
						this.goodsNumber = 0
					}
				}
			})
		},
		getGoodsCoupon() {
			this.$api.getGoodsCoupon({
				goodsId: this.goodsId
			}, (res) => {
				if (res.code) {
					this.GoodsCoupon = res.data
				}
			})
		},
		...mapActions({
			addCardAction: 'addCardAction',
			setUserInfoAction: 'setUserInfoAction',
			emptyAllCardAction: 'emptyAllCardAction'
		})
	},
	onPageScroll(e) {
		let scroll = e.scrollTop <= 0 ? 0 : e.scrollTop;
		let opcity = scroll / this.scrollH;
		if (this.opcity >= 1 && opcity >= 1) {
			return;
		}
		this.opcity = opcity;
		this.iconOpcity = 0.5 * (1 - opcity < 0 ? 0 : 1 - opcity)
	}
}
